<template>
  <v-card class="mx-auto mt-5">
    <v-row>
      <v-col>
        <v-data-table
          :headers="headers"
          :items="
            turnoSelecionado in linhasMinimos
              ? linhasMinimos[turnoSelecionado]
              : []
          "
          hide-default-footer
          disable-pagination
          disable-filtering
          disable-sort
          class="tabela"
          dense
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title>Mínimos</v-toolbar-title>
              <v-icon class="ml-1" @click="dialogDeleteMinimums = true"
                >mdi-delete</v-icon
              >
              <v-spacer />
              <v-col cols="3">
                <v-select
                  v-if="turnos != null && turnos.length > 1"
                  v-model="turnoSelecionado"
                  :items="turnos"
                  item-text="name"
                  item-value="id"
                />
              </v-col>
            </v-toolbar>
          </template>

          <template v-slot:[`item.id_professor`]="props">
            {{ professoresLookup[props.item.id_professor] }}
          </template>

          <template v-for="dia in diasSemana" v-slot:[`item.${dia.id}`]="props">
            <v-text-field
              :key="dia.id"
              type="number"
              step="1"
              min="0"
              v-model.number="props.item[dia.id].valor"
              :append-outer-icon="
                props.item[dia.id].valor > 0
                  ? props.item[dia.id].obrigatorio
                    ? 'mdi-exclamation-thick'
                    : 'mdi-help'
                  : 'mdi-blank'
              "
              flat
              dense
              class="myInput"
              @change="changed"
              @click:append-outer="
                setMandatory(
                  props.item.id_professor,
                  dia.id,
                  !props.item[dia.id].obrigatorio
                )
              "
            ></v-text-field>
          </template>
        </v-data-table>
      </v-col>
      <v-col>
        <v-data-table
          :headers="headers"
          :items="linhasLimitesDiarios"
          hide-default-footer
          disable-pagination
          disable-filtering
          disable-sort
          class="tabela"
          dense
          :key="tabelasReload"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title>Limites diários</v-toolbar-title>
              <v-icon class="ml-1" @click="dialogDeleteLimits = true"
                >mdi-delete</v-icon
              >
              <v-spacer />
            </v-toolbar>
          </template>

          <template v-slot:[`item.id_professor`]="props">
            {{ professoresLookup[props.item.id_professor] }}
          </template>

          <template v-for="dia in diasSemana" v-slot:[`item.${dia.id}`]="props">
            <v-text-field
              :key="dia.id"
              type="number"
              step="1"
              min="0"
              v-model.number="props.item[dia.id]"
              @change="changed"
              flat
              dense
              class="myInput"
            ></v-text-field>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
    <v-dialog v-model="dialogDeleteMinimums" max-width="500px">
      <v-card>
        <v-card-title class="text-h6">
          Limpar mínimos no turno selecionado?
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="dialogDeleteMinimums = false"
            >Cancelar</v-btn
          >
          <v-btn color="blue darken-1" text @click="clearMinimums">OK</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogDeleteLimits" max-width="500px">
      <v-card>
        <v-card-title class="text-h6"> Limpar limites diários? </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="dialogDeleteLimits = false"
            >Cancelar</v-btn
          >
          <v-btn color="blue darken-1" text @click="clearLimits">OK</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import helper from "@/mixins/helper";

export default {
  name: "MinimosMaximos",
  props: ["id"],
  mixins: [helper],
  data() {
    return {
      linhasMinimos: {},
      linhasLimitesDiarios: [],
      tabelasReload: 0,
      dialogDeleteMinimums: false,
      dialogDeleteLimits: false,
    };
  },

  mounted() {
    this.load();
  },

  computed: {
    headers() {
      return [{ text: "Professor", value: "id_professor" }].concat(
        this.diasSemana
          .map((dia) => {
            return { text: dia.name, value: dia.id };
          })
          .slice(0, this.numero_dias)
      );
    },
  },

  methods: {
    async load(isReload = false) {
      try {
        this.setLoading(isReload);
        this.saved();
        await this.getStructure();
        const resp = await this.$axios.get("/limites/" + this.id);
        const { minimos, limitesDiarios } = resp.data;
        const linhasMinimos = {};
        const linhasLimitesDiarios = [];

        this.turnos.forEach((turno) => {
          const idTurno = turno.id;
          linhasMinimos[idTurno] = [];
          this.professores.forEach((professor) => {
            const linha = { id_professor: professor.id };
            for (let indexDia = 0; indexDia < this.numero_dias; indexDia++) {
              linha[this.diasSemana[indexDia].id] = {
                valor: 0,
                obrigatorio: false,
              };
            }
            linhasMinimos[idTurno].push(linha);
          });
        });
        this.professores.forEach((professor) => {
          const linha = { id_professor: professor.id };
          for (let indexDia = 0; indexDia < this.numero_dias; indexDia++) {
            linha[this.diasSemana[indexDia].id] = null;
          }
          linhasLimitesDiarios.push(linha);
        });

        for (let linha of minimos) {
          const minimo = linhasMinimos[linha.id_turno].find(
            (l) => l.id_professor === linha.id_professor
          )[linha.dia];
          minimo.valor = linha.valor;
          minimo.obrigatorio = linha.obrigatorio === 1;
        }

        for (let linha of limitesDiarios) {
          linhasLimitesDiarios.find(
            (l) => l.id_professor === linha.id_professor
          )[linha.dia] = linha.valor;
        }

        this.linhasMinimos = linhasMinimos;
        this.linhasLimitesDiarios = linhasLimitesDiarios;
        this.tabelasReload += 1;
      } catch (e) {
        console.error(e);
      } finally {
        this.clearLoading();
      }
    },
    async save(reload = true) {
      const { linhasMinimos, linhasLimitesDiarios, id } = this;
      const resp = await this.$axios.post("/limites", {
        linhasMinimos,
        linhasLimitesDiarios,
        id,
      });
      if (resp.status === 200 && reload) {
        this.load(true);
      }
    },
    setMandatory(professor, dia, valor) {
      this.changed();
      this.linhasMinimos[this.turnoSelecionado].find(
        (linha) => linha.id_professor === professor
      )[dia].obrigatorio = valor;
    },
    clearMinimums() {
      this.changed();
      this.dialogDeleteMinimums = false;
      for (
        let i = 0;
        i < this.linhasMinimos[this.turnoSelecionado].length;
        i++
      ) {
        for (const dia in this.linhasMinimos[this.turnoSelecionado][i]) {
          if (dia === "id_professor") {
            continue;
          }

          this.linhasMinimos[this.turnoSelecionado][i][dia] = {
            obrigatorio: false,
            valor: 0,
          };
        }
      }
    },
    clearLimits() {
      this.changed();
      this.dialogDeleteLimits = false;
      for (let i = 0; i < this.linhasLimitesDiarios.length; i++) {
        for (const dia in this.linhasLimitesDiarios[i]) {
          if (dia === "id_professor") {
            continue;
          }

          this.linhasLimitesDiarios[i][dia] = null;
        }
      }
    },
  },
};
</script>

<style scoped>
.tabela:deep() td:not(:last-child) {
  border-right: thin solid rgba(0, 0, 0, 0.12);
}

.myInput:deep() input {
  cursor: pointer;
}

.myInput:deep() .v-input__slot::before {
  border-style: none !important;
}

.myInput:deep() input::-webkit-outer-spin-button,
.myInput:deep() input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.myInput:deep() input[type="number"] {
  -moz-appearance: textfield;
}
</style>
